import React from 'react'
import Nav from '../components/Nav'
import Project from '../components/Project'
import { projectData } from '../data.js'

const renderProjectList = () => {
  return projectData.map(({ imageSource, caption, description, tags }) => {
    return (
      <Project
        imageSource={imageSource}
        caption={caption}
        description={description}
        key={caption}
        tags={tags}
      />
      //I miss punning from Reason ^^
    )
  })
}

class Projects extends React.Component {
  state = {
    filter: null,
  }
  render() {
    return (
      <>
        <Nav />
        <main role="main">
          <section className="tagPickerContainer">
            <article>My favorite technologies:</article>
            <div className="tagPickerList">
              <button className="tagPickerListItem">React</button>
              <button className="tagPickerListItem">NextJS</button>
            </div>
          </section>
          <section className="projectList">{renderProjectList()}</section>
        </main>
        <style jsx="true">{`
          main {
            display: flex;
            flex-direction: column;
            align-items: center;
          }
          .tagPickerContainer {
            width: 80%;
            text-align: center;
            padding: 1rem;
          }
          .tagPickerList {
            display: flex;
            justify-content: center;
            margin-top: 1rem;
          }
          .tagPickerListItem {
            margin: 0 1rem;
            padding: 0.5rem 1rem;
          }
          .projectList {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
          }
        `}</style>
      </>
    )
  }
}

export default Projects
